<template>
  <div class="mv-container">
    <div class="mv-wrap">
      <!--  -->
      <div class="info-wrap">
        <div class="mv-info">
          <h2 class="title">{{ song.name }}</h2>
          <span class="date" v-if="('ar' in song)&&('name' in song.ar[0])">{{ song.ar[0].name }}</span>
          <span class="number" v-if="('al' in song)&&('name' in song.al)">{{ song.al.name }}</span>
        </div>
      </div>
      <div class="video-wrap">
        <ul id="lrclist" style="transform: translateY(0px);" ref="lrclist">
          <!--  -->
        </ul>
      </div>

      <!--  -->
      <div class="comment-wrap" v-if="hotComments !== undefined && hotComments.length !== 0">
        <p class="title">
          
          <span class="number">({{ hotComments.length }})</span>
        </p>
        <div class="comments-wrap">
          <!--  -->
          <div class="item" v-for="(item, index) in hotComments" :key="index">
            <div class="icon-wrap">
              <!--  -->
              <img :src="item.user.avatarUrl + '?param=50y50'" alt />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}</span>
                <span class="comment">{{ item.content }}</span>
              </div>
              <!--  -->
              <div class="re-content" v-if="item.beReplied.length !== 0">
                <span class="name">{{ item.beReplied[0].user.nickname }}</span>
                <span class="comment">{{ item.beReplied[0].content }}</span>
              </div>
              <div class="date">{{ item.time | dateFormat }}</div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="comment-wrap">
        <p class="title">
          
          <span class="number">({{ total }})</span>
        </p>
        <div class="comments-wrap">
          <!--  -->
          <div class="item" v-for="(item, index) in comments" :key="index">
            <div class="icon-wrap">
              <!--  -->
              <img :src="item.user.avatarUrl + '?param=50y50'" alt />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}</span>
                <span class="comment">{{ item.content }}</span>
              </div>
              <!--  -->
              <div class="re-content" v-if="item.beReplied.length !== 0">
                <span class="name">{{ item.beReplied[0].user.nickname }}</span>
                <span class="comment">{{ item.beReplied[0].content }}</span>
              </div>
              <div class="date">{{ item.time | dateFormat }}</div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <el-pagination
        v-if="total > 0"
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="pageNum"
        :page-size="pageSize"
      ></el-pagination>
    </div>
    <!--  -->
    <div class="mv-recommend">
      <h3 class="title"></h3>
      <div class="mvs">
        <div class="items">
          <div
            class="item"
            v-for="(item, index) in simiSongs"
            :key="index"
            @click="playMusic(item.id)"
          >
            <div class="img-wrap">
              <img :src="item.album.blurPicUrl  + '?param=80y80'" alt />

              <span class="iconfont icon-play"></span>
              <!-- <div class="num-wrap">
                <div class="iconfont icon-play"></div>
              </div>-->
            </div>
            <div class="info-wrap">
              <div class="name">{{ item.name }}</div>
              <div class="singer">{{ item.artists[0].name }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getLyrics,
  getComment,
  getHotComment,
  getSongDetail,
  getSimiSong,
  getMusicUrl
} from "@/network/request";

export default {
  data() {
    return {
      //id
      id: "",
      // 
      total: 20,
      // 
      pageNum: 1,
      // 
      pageSize: 10,
      // 
      lyric: "",
      //
      hotComments: [],
      //
      comments: [],
      //
      song: {},
      //
      simiSongs: []
    };
  },
  watch: {
    $route() {
      this.id = this.$route.query.id;
      this._getSongDetail();
      this._getLyrics();
      this._getComment();
      this._getHotComment();
      this._getSimiSong();
    }
  },
  created() {
    this.id = this.$route.query.id;
    this._getSongDetail();
    this._getComment();
    this._getHotComment();
    this._getSimiSong();
  },
  mounted() {
    this._getLyrics();
  },
  methods: {
    // 
    handleCurrentChange(val) {
      this.pageNum = val;
      this._getComment();
    },

    // 
    async _getSongDetail() {
      const { data: resp } = await getSongDetail(this.id);
      this.song = resp.songs[0];
    },

    // 
    async _getLyrics() {
      const { data: resp0 } = await getLyrics(this.id);
      this.lyric = resp0.nolyric ? "" : resp0.lrc.lyric;

      let lrcTime = []; //
      let ul = document.getElementById("lrclist"); //ul

      let i = 0;
      this.lyric = this.lyric.split(/[(\r\n)\r\n]+/);
      this.lyric.forEach((value, key) => {
        //lrc
        lrcTime[i++] =
          parseFloat(value.substr(1, 3)) * 60 +
          parseFloat(value.substring(4, 10)); //00:00.00000.000
        ul.innerHTML +=
          "<li><p>" + this.lyric[key].replace(/\[.*?\]/g, "") + "</p></li>"; //ul
      });
      lrcTime[lrcTime.length] = lrcTime[lrcTime.length - 1] + 3; //
      this.$store.commit("changeLrcTime", lrcTime);
    },

    // 
    async _getComment() {
      const { data: resp1 } = await getComment(
        this.id,
        (this.pageNum - 1) * 10
      );
      this.total = resp1.total;
      this.comments = resp1.comments;
    },

    // 
    async _getHotComment() {
      const { data: resp2 } = await getHotComment(this.id);
      this.hotComments = resp2.hotComments;
    },

    // 
    async _getSimiSong() {
      const { data: resp3 } = await getSimiSong(this.id);
      this.simiSongs = resp3.songs;
    },

    // 
    async playMusic(id) {
      const { data: res } = await getMusicUrl(id);
      if (!res.data[0].url) {
        return this.$message.error("VIP ");
      }

      this.$store.commit("changeUrl", res.data[0].url);

      this.$router.push(`/song?id=${id}`);
    }
  },
  filters: {
    //
    dateFormat: function(value) {
      const dt = new Date(value);

      const y = dt.getFullYear();
      const m = (dt.getMonth() + 1 + "").padStart(2, "0");
      const d = (dt.getDate() + "").padStart(2, "0");

      const hh = (dt.getHours() + "").padStart(2, "0");
      const mm = (dt.getMinutes() + "").padStart(2, "0");
      const ss = (dt.getSeconds() + "").padStart(2, "0");

      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
    }
  }
};
</script>

<style scoped>
.mv-container {
  display: flex;
  padding: 0 150px;
}

.mv-container .title {
  margin-bottom: 20px;
}

.mv-container .mv-wrap {
  width: 700px;
  margin-right: 35px;
}

.mv-container .mv-wrap .date {
  margin-right: 25px;
}

.mv-container .mv-wrap .date,
.mv-container .mv-wrap .number {
  color: #bebebe;
  font-size: 14px;
}

.mv-container .mv-wrap .video-wrap {
  width: 100%;
  height: 340px;
  overflow: hidden;
  font-size: 18px;
  line-height: 25px;
  margin-bottom: 80px;
}

.mv-container .mv-wrap .video-wrap ul {
  transition-duration: 600ms;
}

.mv-container .mv-wrap .video-wrap ul,
.mv-container .mv-wrap .video-wrap li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mv-container .mv-wrap .video-wrap li.on {
  color: red;
}

.mv-container .mv-wrap .video-wrap p {
  overflow: hidden;
  text-overflow: ellipsis;
  height: 32px;
  line-height: 32px;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  text-align: center;
}

pre {
  font-size: 18px;
  line-height: 25px;
}

.mv-container .mv-wrap .info-wrap {
  margin-bottom: 50px;
}

.mv-container .mv-wrap .info-wrap .singer-info {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
}

.mv-container .mv-wrap .info-wrap .singer-info .avatar-wrap {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-right: 10px;
  overflow: hidden;
}

.mv-container .mv-wrap .info-wrap .singer-info .avatar-wrap img {
  height: 100%;
}

.mv-container .mv-wrap .info-wrap .mv-info .title {
  font-size: 30px;
}

.mv-container .mv-wrap .info-wrap .mv-info .desc {
  font-size: 15px;
  margin-top: 20px;
}

.mv-container .mv-wrap .comment-wrap {
  margin-bottom: 70px;
}

.mv-container .mv-wrap .comment-wrap .title {
  font-size: 25px;
}

.mv-container .mv-wrap .comment-wrap .title .number {
  color: black;
  font-size: 20px;
}

.mv-container .mv-wrap .comment-wrap .item {
  display: flex;
  padding-top: 20px;
}

.mv-container .mv-wrap .comment-wrap .item .icon-wrap {
  margin-right: 15px;
}

.mv-container .mv-wrap .comment-wrap .item .icon-wrap img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.mv-container .mv-wrap .comment-wrap .item:not(:last-child) .content-wrap {
  border-bottom: 1px solid #f2f2f1;
}

.mv-container .mv-wrap .comment-wrap .item .content-wrap {
  padding-bottom: 20px;
  flex: 1;
}

.mv-container .mv-wrap .comment-wrap .item .content-wrap .name {
  color: #517eaf;
  font-size: 14px;
}

.mv-container .mv-wrap .comment-wrap .item .content-wrap .comment {
  font-size: 14px;
}

.mv-container .mv-wrap .comment-wrap .item .content-wrap .content,
.mv-container .mv-wrap .comment-wrap .item .content-wrap .re-content {
  margin-bottom: 10px;
}

.mv-container .mv-wrap .comment-wrap .item .content-wrap .re-content {
  padding: 10px;
  background-color: #e6e5e6;
  border-radius: 5px;
}

.mv-container .mv-recommend {
  flex: 1;
}

.mv-container .mv-recommend .mvs .items {
  display: flex;
  flex-wrap: wrap;
}

.mv-container .mv-recommend .mvs .items .item {
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}

.mv-container .mv-recommend .mvs .items .item:hover {
  background-color: #f5f5f5;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap {
  width: 70px;
  position: relative;
  margin-right: 10px;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap > .icon-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  color: #dd6d60;
  font-size: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
  opacity: 0;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap > .icon-play::before {
  transform: translateX(3px);
}

.mv-container .mv-recommend .mvs .items .item .img-wrap:hover > .icon-play {
  opacity: 1;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap img {
  width: 100%;
  border-radius: 5px;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap .num-wrap {
  position: absolute;
  color: white;
  top: 0;
  right: 0;
  display: flex;
  align-content: center;
  font-size: 15px;
  padding-right: 5px;
  padding-top: 2px;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap .num-wrap .icon-play {
  font-size: 12px;
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.mv-container .mv-recommend .mvs .items .item .img-wrap .time {
  position: absolute;
  bottom: 5px;
  right: 5px;
  color: white;
  font-size: 15px;
}

.mv-container .mv-recommend .mvs .items .item .info-wrap {
  flex: 1;
}

.mv-container .mv-recommend .mvs .items .item .info-wrap .name {
  font-size: 15px;
  line-height: 40px;
}

.mv-container .mv-recommend .mvs .items .item .info-wrap .singer {
  font-size: 14px;
  color: #c5c5c5;
  line-height: 40px;
}
</style>
