<template>
  <div class="result-container">
    <div class="title-wrap">
      <h2 class="title">"{{ $route.query.q }}"</h2>
      <!--  -->
      <el-alert title="" type="success" center show-icon :closable="true"></el-alert>
    </div>
    <!--  -->
    <template>
      <el-table
        row-key="id"
        :data="songs"
        style="width: 100%;font-size: 14px"
        @row-dblclick="playMusic"
      >
        <el-table-column type="index">
          <template slot-scope="scope">
            <span>{{ (pageNum - 1) * pageSize + scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <span>{{ scope.row.name }}</span>
            <span v-if="scope.row.mvid !== 0" class="iconfont icon-mv"></span>
          </template>
        </el-table-column>
        <el-table-column prop="artists[0].name" label="" width="200"></el-table-column>
        <el-table-column prop="album.name" label="" width="400"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.duration | playTimeFormat }}</span>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <!--  -->
    <el-pagination
      @current-change="handleCurrentChange"
      layout="prev, pager, next"
      :total="total"
      v-if="total > 0"
      :current-page="pageNum"
      :page-size="pageSize"
    ></el-pagination>
  </div>
</template>

<script>
import { search, getMusicUrl } from "@/network/request";
export default {
  data() {
    return {
      total: 0, //
      pageNum: 1, //
      pageSize: 9, //
      songs: [] //
    };
  },
  created() {
    this._search();
  },
  watch: {
    //
    $route() {
      this._search();
    }
  },
  methods: {
    // 
    handleCurrentChange(val) {
      // 
      this.pageNum = val;
      // 
      this._search();
    },

    //
    async _search() {
      const { data: resp } = await search(
        this.$route.query.q,
        (this.pageNum - 1) * 9
      );
      if (resp.code !== 200) {
        return;
      }

      this.songs = resp.result.songs;
      this.total = resp.result.songCount;
    },

    async playMusic(row) {
      // 
      const { data: res } = await getMusicUrl(row.id);
      if (!res.data[0].url) {
        return this.$message.error("VIP ");
      }
      // 
      this.$store.commit("changeUrl", res.data[0].url);
      //
      this.$router.push(`/song?id=${row.id}`);
    }
  },
  filters: {
    //
    playTimeFormat: function(value) {
      let duration = value;
      let min = parseInt(duration / 1000 / 60);
      if (min < 10) {
        min = "0" + min;
      }
      let sec = parseInt((duration / 1000) % 60);
      if (sec < 10) {
        sec = "0" + sec;
      }
      return `${min}:${sec}`;
    }
  }
};
</script>

<style scoped>
.result-container {
  padding: 0 150px;
}
.icon-mv {
  color: red;
  padding-left: 5px;
}
</style>
