import axios from "axios";

axios.defaults.baseURL = 'https://autumnfish.cn'

// axios.defaults.baseURL = "/api";
axios.defaults.withCredentials = true;
axios.defaults.crossDomain = true;

// 
export function getBanner() {
  return axios.get("/banner");
}

// 
export function getPersonalizedNewsong(param) {
  return axios.get("/personalized/newsong", param);
}

// 
export function getMusicUrl(id) {
  return axios.post(`/song/url?id=${id}`);
}

// 
export function search(keywords, offset) {
  return axios.get(`/search?keywords=${keywords}&limit=9&offset=${offset}`);
}

// 
export function getLyrics(id) {
  return axios.get(`/lyric?id=${id}`);
}

// 
export function getComment(id, offset) {
  return axios.get(`/comment/music?id=${id}&limit=10&offset=${offset}`);
}

// 
export function getHotComment(id) {
  return axios.get(`/comment/hot?id=${id}&type=0`);
}

// 
export function getSongDetail(id) {
  return axios.get(`/song/detail?ids=${id}`);
}

// 
export function getSimiSong(id) {
  return axios.get(`/simi/song?id=${id}`);
}